<template>
  <div :hidden="table.length== 0">
    <json-excel
    class="btn btn-primary"
    :header="header"
    :data="tables"
    :name="filename + '.xls'"
    :footer="tableLegend"
    >
    Excel
    </json-excel>

    <json-csv
    class="btn btn-secondary"
    :data="tables"
    :name="filename + '.csv'"
    >
    Csv
    </json-csv>
  </div>
  </template>
<script>
import JsonExcel from 'vue-json-excel'
import JsonCsv from 'vue-json-csv'
export default {
  name: 'export',
  props: ['table', 'filename', 'footer', 'header', 'legend'],
  components: {
    JsonExcel,
    JsonCsv
  },
  computed: {
    tables () {
      const localTable = []

      if (this.table) {
        const tableHeaders = Object.keys(this.table[0])

        const headerRow = tableHeaders.map((header) => {
          const colspan = header.length > 12 ? 'colspan="2"' : ''
          return `<th align="center" bgcolor="#ccffff" scope="col" ${colspan}>${header}</th>`
        }).join('')

        let resultado = `<table><thead><tr>${headerRow}</tr></thead><tbody>`

        this.table.forEach((row) => {
          const rowValues = tableHeaders.map((header, index) => {
            const value = row[header]
            const colspan = tableHeaders[index].length > 12 ? 'colspan="2"' : ''
            return `<td align="center" ${colspan}>${value}</td>`
          })
          const rowData = rowValues.join('')
          resultado += `<tr>${rowData}</tr>`
        })

        resultado += '</tbody>'

        resultado += '</table>'
        localTable.push({ resultado })

        if (!this.footer) { return localTable }
        if (this.table.length > 0) {
          localTable.length = 0
          const tempTable = JSON.parse(JSON.stringify(this.table))

          const footerRow = {}

          this.footer.forEach((footerItem, index) => {
            const header = tableHeaders[index]
            footerRow[header] = '<b>' + footerItem + '</b>'
          })

          tempTable.push(footerRow)

          let resultado = `<table><thead><tr>${headerRow}</tr></thead><tbody>`

          tempTable.forEach((row) => {
            const rowValues = tableHeaders.map((header, index) => {
              const value = row[header]
              const colspan = header.length > 12 ? 'colspan="2"' : ''
              return `<td align="center" ${colspan}>${value}</td>`
            })
            const rowData = rowValues.join('')
            resultado += `<tr>${rowData}</tr>`
          })

          resultado += '</tbody></table>'
          localTable.push({ resultado })

          return localTable
        }
      }
      return localTable
    },
    tableLegend () {
      return this.legend
    }
  }
}
</script>
